<template>
  <v-main class="pt-0">
    <v-container>
      <Header/>
      <v-row>
        <v-col xl="2" lg="3" md="4" sm="12" xs="12" :order="sidebar">
          <Sidebar/>
        </v-col>
        <v-col xl="8"  lg="6" md="8" sm="12" xs="12" :order="main">
          <router-view :selectedItem="selectedItem"/>
        </v-col>
        <v-col xl="2" lg="3" md="4" sm="12" xs="12" :order="aside">
          <router-view name="aside" @selectItem="onSelectItem"/>
        </v-col>
      </v-row>
    </v-container>
    <Footer/>
  </v-main>
</template>
<script>
import Header from "./components/Header";
import Sidebar from "./components/Sidebar";
import Footer from "./components/Footer";

export default {
  name: 'Default',
  data: () => ({
    selectedItem: null
  }),
  components: {Footer, Sidebar, Header},
  methods: {
    onSelectItem(item) {
      this.selectedItem = item;
    }
  },
  computed: {
    sidebar() {
      if (this.$route.path === '/resume' || this.$vuetify.breakpoint.width > this.$vuetify.breakpoint.thresholds.sm) {
        return 1;
      }
      return 3;
    },
    main() {
      if (this.$route.path === '/resume' || this.$vuetify.breakpoint.width > this.$vuetify.breakpoint.thresholds.sm) {
        return 2;
      }
      return 2;
    },
    aside() {
      if (this.$route.path === '/resume' || this.$vuetify.breakpoint.width > this.$vuetify.breakpoint.thresholds.sm) {
        return 3;
      }
      return 1;
    }
  }
};
</script>